	module.exports = {"data":{"site":{"siteMetadata":{"title":"Gatsby Starter - Forty by HTML5 UP","author":"Hunter Chang"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/imagesizer-feedback-screen-fail/index.md absPath of file >>> MarkdownRemark","html":"<p>This is a 'public service announcement'.  Basically, the ImageSizer feedback screen hasn't always been working.  If you've used it already to send your feedback, there's a good chance that the message wasn't delivered, regardless of the confirmation screen you may have seen.  </p>\n<p>It's all fixed now, but if you've sent feedback or have a burning comment to make then please, send it again. (And sorry for the trouble.)</p>\n<img class=\"alignnone size-full wp-image-835\" title=\"ImageSizer feedback screen\" src=\"http://uploads.psyked.co.uk/2009/03/feedback-screen.jpg\" alt=\"ImageSizer feedback screen\" width=\"500\" height=\"280\">\n<p>I'm planning on a few further updates for ImageSizer soon, to make this process a little better...</p>","frontmatter":{"title":"ImageSizer - feedback screen fail. :-/","date":"March 29, 2009","tags":["imagesizer"]}}},"pathContext":{}}

